@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
body {
  margin: 0;
  background: rgb(89, 89, 89);
  font-family: 'Roboto', sans-serif;
  overflow: hidden;
}
.app{
  display: flex;
  gap:32px;
  width:100vw;
  height:100vh;
}
.word{
  user-select: none;
  -webkite-user-drag:none;
  display: flex;
  flex-direction: column;
  gap:8px;
  padding:8px 16px;
  background:rgb(72, 72, 72);
  position: relative;
  &__name{
    font-size:32px;
    color:white;
    position: absolute;
    top:-40px;
    left:-24px;
  }
  &__bits{
    display: flex;
    gap:8px;
    justify-content: center;
    user-select: none;
  }
  &__text{
    display: flex;
    gap:8px;
    justify-content: center;
    user-select: none;
    font-size:24px;
  }
}
.bit{
  width:25px;
  height:50px;
  
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size:32px;
  &-true{
    background:rgb(26, 26, 26);
    color : white;
  }
  &-false{
    background:rgb(241, 241, 241);
    color : rgb(26, 26, 26);
  }
}
.add{
  background:white;
  user-select: none;
  display: flex;
  justify-content: center;
  align-items: center;
  width:52px;
  height:64px;
  font-size:24px;
  position: relative;
  color:black;
}
.panel{
  
  z-index:48;
  color:white;
  height:64px;
  background-color: rgb(40, 40, 40);
  display: flex;
  justify-content: flex-start;
  position: fixed;
  left:0;
  bottom: 0;
  width:100vw;
  align-items: center;
  padding:16px;
  gap:16px;
  .input-group{
    max-width: 300px;
  }
  h2{
    font-size:32px;
  }
  p{
    margin:0;
    font-size:16px;
  }
  .f-grow{
    flex-grow:1;
  }
}
.workspace{
  flex-grow: 1;
}
.baton{
  //border:1px solid red;
  opacity: 0;
  &:hover{
    opacity: 1;
  }
  transition: opacity 0.3s ease-in-out;
  user-select: none;
  -webkite-user-drag:none;
  display: flex;
  justify-content: center;
  align-items: center;
  width:32px;
  height:64px;
  position: absolute;
  font-size:24px;
  z-index:32;
  color:white;
  &-to{
    top:-48px;
  }
  &-from{
    bottom:-48px;
  }
}
.delete{
  position: absolute;
  bottom:2px;
  left:4px;
  color:white;
}
.btn-wrapper{
  -webkite-user-drag:none;
  position: absolute;
  display: flex;
  justify-content: center;
  border:1px solid transparent;
  transition: border 0.3s ease-in-out;
  &:hover{
    border:1px solid var(--color);
  }
}
.drag-knob{
  //position: absolute;
  //left:0px;
  //top:50%;
  background:rgb(23, 23, 23);
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 16px;
  width:24px;
  color:white;
  
}
.drg-color{
  width:4px;
  background:var(--color);
}
.line{
  transform-origin: 0 50%;
  position: absolute;
  background: var(--color);
  height:4px;
}
